	module.exports = {"data":{"talkJson":{"description":"A discussion of asyncio, the new async/await syntax in Python 3.5, and a future view of async Python without the asyncio package.  Plus, live coding. \n\nCode samples at https://gist.github.com/dabeaz/fc5c08040effca799759","meta":{"duration":"PT56M30S"},"presenter":{"name":null},"slug":"keynote-at-pycon-brasil-2015-screencast","source":{"id":"lYe8W04ERnY","provider":"youtube"},"tag":["python","asyncio","python3","PyCon (Conference Series)"],"title":"Keynote at PyCon Brasil 2015 (Screencast)"},"allTalkJson":{"edges":[{"node":{"description":"Videos from DjangoCon Europe 2013 are kindly provided by Heroku, a cloud application platform -- a new way of building and deploying web apps: http://heroku.com/","meta":{"duration":"PT24M31S"},"presenter":{"name":null},"source":{"id":"LguTqd6CtOw","provider":"youtube"},"slug":"djangocon-eu-2013-steven-holmes-does-your-stuff-scale","tag":["djangocon","djangocon europe","djangocon europe 2013","django","python","heroku","conference","steven holmes"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/LguTqd6CtOw/default.jpg","height":90,"width":120}},"title":"DjangoCon EU 2013: Steven Holmes - Does your stuff scale?"}},{"node":{"description":"The Cython compiler is the most widely used static compiler for Python. It is used to speed up Python code and to extend CPython with fast native extension modules that process huge amounts of data all around the world. This talk by one of the core developers gives an intro to using the compiler and an overview of its major features.","meta":{"duration":"PT48M1S"},"presenter":{"name":null},"source":{"id":"ZHpkLX2VFMU","provider":"youtube"},"slug":"the-cython-compiler-for-python","tag":["python","euro"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/ZHpkLX2VFMU/default.jpg","height":90,"width":120}},"title":"The Cython Compiler for Python"}},{"node":{"description":"In this talk Francesc Campoy Flores presents Go for Pythonistas.\n\nSlides are available at http://talks.golang.org/2013/go4python.slide\n\nHe looks at the similarities and differences between Python and Go, skipping the basics and focusing on some advanced, in-depth topics.\nPlus, we'll get our hands dirty with some Go exercises.\n\nIt displays some techniques typical from dynamic languages as generators, decorators, and monkey patching and how they can be implemented in Go.","meta":{"duration":"PT51M17S"},"presenter":{"name":"Francesc Campoy Flores"},"source":{"id":"elu0VpLzJL8","provider":"youtube"},"slug":"go-for-pythonistas","tag":["golang","python"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/elu0VpLzJL8/default.jpg","height":90,"width":120}},"title":"Go for Pythonistas"}},{"node":{"description":"Juan Manuel Santos - Salting things up in the sysadmin's world\n[EuroPython 2015]\n[21 July 2015]\n[Bilbao, Euskadi, Spain]\n\nSaltStack is a thriving configuration management system written in\nPython that leverages YAML and Jinja2 which, by now, probably needs no\nintroduction.\n\nThis talk will cover a brief summary of why we need configuration\nmanagement tools, followed by a full dive into SaltStack, its\nfeatures, pros and cons, how to use it and how to extend it. By the\nend of this talk you will have gone from knowing little or nothing\nabout SaltStack, to being able to deploy your own setup.\n\nThis talk will be targeted to either seasoned Python developers who\nare taking their first steps in the system administration world, or\nestablished system administrators who secretly love Python and prefer\nto stay away of configuration management systems based on other\nlanguages.\nIts advisable that attendees have some familiarity with Python as well\nas with system administration concepts. Also, this presentation will\nbe focused on GNU/Linux systems, so it is expected that attendees are\ncomfortable with some of its concepts.","meta":{"duration":"PT37M21S"},"presenter":{"name":null},"source":{"id":"w1zcUNMwac0","provider":"youtube"},"slug":"juan-manuel-santos-salting-things-up-in-the-sysadmins-world","tag":["saltstack","management","python","configuration","linux","EuroPython2015"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/w1zcUNMwac0/default.jpg","height":90,"width":120}},"title":"Juan Manuel Santos - Salting things up in the sysadmin's world"}},{"node":{"description":"Videos from DjangoCon Europe 2013 are kindly provided by Heroku, a cloud application platform -- a new way of building and deploying web apps: http://heroku.com/","meta":{"duration":"PT31M10S"},"presenter":{"name":null},"source":{"id":"yvMWUGx7Fx8","provider":"youtube"},"slug":"djangocon-eu-2013-brandon-rhodes-keynote","tag":["djangocon","djangocon europe 2013","djangocon europe","python","heroku","keynote","brandon rhodes"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/yvMWUGx7Fx8/default.jpg","height":90,"width":120}},"title":"DjangoCon EU 2013: Brandon Rhodes - Keynote"}}]}},"pathContext":{"slug":"keynote-at-pycon-brasil-2015-screencast","tags":["python","asyncio","python3","PyCon (Conference Series)"]}}

